import React from 'react';
import classes from './DrawerToggle.css'

const DrawerToggle = props => (
    <div className={classes.DrawerToggle} onClick={props.toggleDrawer}>
        <div></div>
        <div></div>
        <div></div>
    </div>
);

export default DrawerToggle;



